@font-face {
  font-family: "Avenir";
  font-weight: 300px;
  src: url(fonts/Avenir-Book.ttf);
}

/* @font-face {
  font-family: "Avenir";
  font-weight: bold;
  src: url(fonts/Avenir-Heavy.ttf);
} */

.body {
  font-weight: 300;
  box-sizing: border-box;
  margin-top: 20px;
}

.search {
  font-family: Avenir, sans-serif;
  background: #fff;
  width: 100%;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  font-weight: normal;
}

.search::placeholder {
  font-family: Avenir, sans-serif;
  font-weight: normal;
  color: #3d4e61;
}

.big--search {
  font-size: 16px;
  line-height: 22px;
  height: 70px;
  margin-bottom: 20px;
  padding-left: 62px;
}

.search:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.search:focus {
  outline: none;
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-family: "Avenir-heavy", sans-serif;
  font-weight: bold;
  src: url(fonts/Avenir-Heavy.ttf);
}

.small--search {
  font-size: 14px;
  line-height: 19px;
  height: 42px;
  padding-left: 33px;
}

.loop {
  position: absolute;
}

.big--loop {
  width: 19px;
  height: 19px;
  top: 45px;
  left: 33px;
}

.small--loop {
  width: 11px;
  height: 11px;
  top: 125px;
  left: 21px;
}

.container {
  margin-top: 20px;
}
